@import url(//db.onlinewebfonts.com/c/b593a614952261da06c53a02d1954f44?family=SEGA+LOGO+FONT);
@font-face {
  font-family: "SEGA LOGO FONT";
  font-style: normal;
  font-weight: normal;
  src: url("//db.onlinewebfonts.com/t/b593a614952261da06c53a02d1954f44.eot"); /* IE9*/
  src: url("//db.onlinewebfonts.com/t/b593a614952261da06c53a02d1954f44.eot?#iefix")
      format("embedded-opentype"), /* IE6-IE8 */
    url("//db.onlinewebfonts.com/t/b593a614952261da06c53a02d1954f44.woff2")
      format("woff2"), /* chrome、firefox */
    url("//db.onlinewebfonts.com/t/b593a614952261da06c53a02d1954f44.woff")
      format("woff"), /* chrome、firefox */
